<template><section><h2>TabBar 使用指南</h2>
<p>用于固定在 webapp 底部的导航控件，由 <code>TabBar</code> 和 <code>TabBarItem</code> 组成。该组件实现切换页面有两种方式：</p>
<ul>
<li>1、通过监听 TabBarItem 的 click 事件，动态修改 TabBarItem 的 selected 属性</li>
<li>2、使用 TabBarItem 的 to 属性指定一个 route，TabBarItem 被点击时会自动跳转到相应的页面</li>
</ul>
<template>
<ExampleBox>
  <div style="padding-bottom: 48px; background-color: #fff">
    <div style="padding: 20px 0; text-align: center;" v-if="selected === 0">
      首页
    </div>
    <div style="padding: 20px 0; text-align: center;" v-if="selected === 1">
      关于 Smart UI
    </div>
    <TabBar bgcolor="#f5f5f5">
      <TabBarItem :selected="selected === 0" title="首页" @click="selected = 0">
        <img slot="icon" src="images/diamond.svg">
        <img slot="icon-selected" src="images/diamond-active.svg">
      </TabBarItem>
      <TabBarItem :selected="selected === 1" title="关于" @click="selected = 1">
        <img slot="icon" src="images/like.svg">
        <img slot="icon-selected" src="images/like-active.svg">
      </TabBarItem>
    </TabBar>
  </div>
</ExampleBox>
</template>


<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.TabBarItem</span><span class="hljs-selector-class">.selected</span> {
  <span class="hljs-attribute">color</span>: <span class="hljs-number">#2b8ff7</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding-bottom 48px; background-color: #fff"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding: 20px 0; text-align: center;"</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"selected === 0"</span>&gt;</span>
      首页
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding: 20px 0; text-align: center;"</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"selected === 1"</span>&gt;</span>
      关于 Smart UI
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">TabBar</span> <span class="hljs-attr">bgcolor</span>=<span class="hljs-string">"#f5f5f5"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">TabBarItem</span> <span class="hljs-attr">:selected</span>=<span class="hljs-string">"selected === 0"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"首页"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"selected = 0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">TabBarItem</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">TabBarItem</span> <span class="hljs-attr">:selected</span>=<span class="hljs-string">"selected === 1"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"关于"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"selected = 1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">TabBarItem</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">TabBar</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>  
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">selected</span>: <span class="hljs-number">0</span>
    }
  },
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h4>使用 to 属性实现页面切换</h4>
<p>这种方式更加简洁，因为 TabBar 可能切换比较频繁，所以这里跳转页面时使用是 <code>$router.replace</code>，不会产生大量多余的历史记录</p>
<template>
<ExampleBox>
  <div style="padding-bottom: 88px; background-color: #fff">
    <TabBar bgcolor="#f5f5f5" class="TabBarDemo2">
      <TabBarItem to="/TabBar/index" :selected="$route.path === '/TabBar/index'" title="首页"></TabBarItem>
      <TabBarItem to="/TabBar/about" :selected="$route.path === '/TabBar/about'" title="关于"></TabBarItem>
    </TabBar>
  </div>
</ExampleBox>
</template>

<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">ExampleBox</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding-bottom: 88px; background-color: #fff"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">TabBar</span> <span class="hljs-attr">bgcolor</span>=<span class="hljs-string">"#f5f5f5"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"TabBarDemo2"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">TabBarItem</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/TabBar/index"</span> <span class="hljs-attr">:selected</span>=<span class="hljs-string">"$route.path === '/TabBar/index'"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"首页"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">TabBarItem</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">TabBarItem</span> <span class="hljs-attr">to</span>=<span class="hljs-string">"/TabBar/about"</span> <span class="hljs-attr">:selected</span>=<span class="hljs-string">"$route.path === '/TabBar/about'"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"关于"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">TabBarItem</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">TabBar</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ExampleBox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.TabBarDemo2</span> <span class="hljs-selector-class">.TabBarItem</span><span class="hljs-selector-class">.selected</span> {
  <span class="hljs-attribute">color</span>: <span class="hljs-number">#fff</span>;
  <span class="hljs-attribute">background-color</span>: pink;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
<h4>设置图标</h4>
<p>图标是可选的。但可以通过 slot 来设置。 <code>slot=&quot;icon&quot;</code> 和 <code>slot=&quot;icon-selected&quot;</code> 分别代表默认图标和选中时的图标。图标可以是 img，也可以用其他的html标签或组件。</p>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">TabBarItem</span> <span class="hljs-attr">:selected</span>=<span class="hljs-string">"selected === 1"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"关于"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"selected = 1"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"icon"</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"images/like.svg"</span> /&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"icon-selected"</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"images/like-active.svg"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">TabBarItem</span>&gt;</span>
</code></pre>
<h4>隐藏 TabBar</h4>
<p>设置 TabBar 的 <code>visible</code> 属性为 false 时可以将其隐藏。比如一个 webapp 的子页面是不需要展示 TabBar 的，我们可以根据当前的route是否是根页面来决定是否隐藏 TabBar。</p>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">TabBar</span> <span class="hljs-attr">fixed</span> <span class="hljs-attr">:visible</span>=<span class="hljs-string">"visible"</span>&gt;</span>
    ...
  <span class="hljs-tag">&lt;/<span class="hljs-name">TabBar</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-comment">// 根页面</span>
<span class="hljs-keyword">const</span> rootRoutes = [
  <span class="hljs-string">'/'</span>,
  <span class="hljs-string">'/About'</span>
]

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  visible() {
      <span class="hljs-comment">// 不在 rootRoutes 里的页面隐藏 TabBar</span>
      <span class="hljs-keyword">return</span> rootRoutes.indexOf(<span class="hljs-keyword">this</span>.$route.path) &gt; <span class="hljs-number">-1</span>
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h4>自定义选中 TabBarItem 的样式</h4>
<p>选中的 TabBarItem 会添加 <code>selected</code> class，所以直接使用自定义的样式覆盖默认样式</p>
<pre><code class="language-css"><span class="hljs-selector-class">.TabBarItem</span><span class="hljs-selector-class">.selected</span> {
  <span class="hljs-attribute">color</span>: <span class="hljs-number">#2b8ff7</span>;
}
</code></pre>
<h4>TabBar 属性：</h4>
<table>
<thead>
<tr>
<th>参数</th>
<th>类型</th>
<th>说明</th>
<th>默认值</th>
<th style="text-align:center">必须</th>
</tr>
</thead>
<tbody>
<tr>
<td>fixed</td>
<td>Boolean</td>
<td>是否固定在底部</td>
<td>false</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td>visible</td>
<td>Boolean</td>
<td>是否显示(webapp中，子页面一般不需要展示TabBar)</td>
<td>true</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td>bgcolor</td>
<td>String</td>
<td>背景颜色，可使用任何 CSS 支持的表示颜色的格式</td>
<td>rgba(247,247,250,.8)</td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>
<h4>TabBarItem 属性：</h4>
<table>
<thead>
<tr>
<th>名称</th>
<th>类型</th>
<th>说明</th>
<th>默认值</th>
<th style="text-align:center">必须</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>String</td>
<td>Tab 的名称</td>
<td></td>
<td style="text-align:center">是</td>
</tr>
<tr>
<td>selected</td>
<td>Boolean</td>
<td></td>
<td>false</td>
<td style="text-align:center">是</td>
</tr>
<tr>
<td>to</td>
<td>String</td>
<td>使用 <code>this.$router.replace</code> 导航到该值所指定的路由</td>
<td></td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>
<h4>TabBarItem 事件：</h4>
<table>
<thead>
<tr>
<th>事件名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>click</td>
<td>点击事件，可在该事件触发时设置TaBar的选中状态</td>
</tr>
</tbody>
</table>
</section></template>
<style>
.TabBarItem.selected {
  color: #2b8ff7;
}
</style><style>
.TabBarDemo2 .TabBarItem.selected {
  color: #fff;
  background-color: pink;
}
</style>
<script>
export default {
  data() {
    return {
      selected: 0,
      visible: true
    }
  },
  methods: {
    toggle() {
      this.visible = !this.visible
    }
  }
}
</script>


